.diff-added {
  padding: 0 5px;
  margin: 0 -5px;
  background-color: #e6ffed;
  overflow: hidden;
}

.diff-removed {
  padding: 0 5px;
  margin: 0 -5px;
  overflow: hidden;
  background-color: #ffeef0;
}

.diff-removed * {
  border-left: none;
  padding-left: 0
}

.diff-added * {
  padding-left: 0;
  border-left: none;
}


.diff-replaced {
  padding-left: 2px;
  margin-left: -5px;
  border-left: 3px solid #f9c513;
}

.diff-replaced .diff-added {
  background-color: #e6ffed;
  margin-left: 0;
  padding-left: 0;
  overflow: visible;
}

.diff-replaced .diff-removed {
  overflow: visible;
  padding-left: 0;
  color: #cb2431;
  margin-left: 0;
  background-color: #ffeef0;
}

.diff-replaced .diff-replaced {
  border-left: none;
  margin-left: 0;
  overflow: visible;
}

.diff-replaced .diff-removed-container {
  padding: 0 5px;
  margin: 0 -5px;
  background-color: #ffeef0;
  box-sizing: border-box;
  overflow: hidden;
}

.diff-replaced .diff-added-container {
  padding: 0 5px;
  margin: 0 -5px;
  background-color: #e6ffed;
  box-sizing: border-box;
  overflow: hidden;
}


.diff-replaced table .diff-added {
  background-color: #e6ffed;
  padding: 0 0;
  margin: 0;
}

.diff-replaced table .diff-removed {
  background-color: #ffeef0;
  margin: 0;
  padding: 0 0;
}

.diff-replaced table tr.diff-replaced {
  padding-left: 0;
}

.diff-replaced del {
  background-color: #fdb8c0;
  text-decoration: none;
  color: #cb2431;
}

.diff-replaced ins {
  background-color: #acf2bd;
  text-decoration: none;
}

.article .diff-replaced .old-paragraph {
  background-color: #ffeef0;
  margin-bottom: 0;
}

.article .diff-replaced .new-paragraph {
  background-color: #e6ffed;
  margin-top: 0;
}